.mains1 {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 60vh;
    width: 500px;
  
    h1 {
      font-size: 2rem;
      margin-bottom: 1rem;
    }
  }
  
  .main1 {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    margin-top: 10px;
    max-width: 450px;
    width: 100%;
  
    .MuiTextField-root {
      width: 100%;
    }
  
    .MuiButton-root {
      background-color: #2196f3; // Material-UI primary color
      color: #fff;
      &:hover {
        background-color: #1565c0; // Material-UI darkened primary color on hover
      }
    }
  }