// CheckoutPage.scss

.checkout-container {
    max-width: 600px;
    margin: 0 auto;
    margin-top: 150px;
    margin-bottom: 150px;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  
    h2 {
      text-align: center;
    }

    form {
      display: flex;
      flex-direction: column;
  
      label {
        margin-top: 10px;
        font-weight: bold;
      }
  
      input,
      textarea {
        margin-top: 5px;
        padding: 10px;
        font-size: 16px;
        border: 1px solid #ccc;
        border-radius: 5px;
      }
  
      .card-details {
        display: flex;
        justify-content: space-between;
  
        div {
          flex: 1;
  
          &:first-child {
            margin-right: 10px;
          }
        }
      }
  
      button {
        margin-top: 20px;
        padding: 10px;
        background-color: #3498db;
        color: #fff;
        font-size: 18px;
        border: none;
        border-radius: 5px;
        cursor: pointer;
  
        &:hover {
          background-color: #2980b9;
        }
      }
    }
  }
  