// @import "../../css-config/mixins.scss";
@import "../../Pages/Home/Mixins/Mixins.scss";

.single-product-main-content{
    margin:  20px 0;
    @include md{
        margin: 75px 0;
    }

    .layout{
        max-width: calc(100% - 20px);
        margin: 0 auto;
        @include md{
            max-width: 1200px;
        }
        .single-product-page{
            display: flex;
            flex-direction: column;
            @include md{
                flex-direction: row;
            }

            .left{
                width: 100%;
                // background-color: rgba(0,0,0,0.08);
                flex-shrink: 0;
                position: relative;
                @include md{
                    width: 400px;
                    height: auto;
                }

                .img{
                    margin-top: 100px;
                    height: 500px;
                    width: 400px;
                }

                .heart{
                    position: absolute;
                    top: 15px;
                    right:15px;
                    // padding: 14px;
                    cursor: pointer;
                 
            
                    svg{
                        font-size: 28px;
                        color: rgba(0,0,0,0.5);
            
                       
                        
                    }
                }

                img{
                    width: 100%;
                    display: block;
                }


            }

            .right{
                display: flex;
                flex-direction: column;
                padding-top: 20px;
                @include md{
                    padding: 0 35px;
                    
                }
                .name{
                    font-size: 20px;
                    line-height: 28px;
                    margin-bottom: 10px;
                    @include md{
                        font-size: 24px;
                        line-height: 32px;
                    }
                }

                .rating{
                    font-size: 16px;
                    line-height: 28px;
                    margin-bottom: 10px;
                    @include md{
                        font-size: 17px;
                        line-height: 32px;
                    }
                }

                .price{
                    font-size: 20px;
                    line-height: 32px;
                    margin-bottom: 10px;
                }
                .desc{
                    font-size: 14px;
                    line-height: 20px;
                    margin-bottom: 10px;
                    color: #6b6b6b;
                    @include md{
                        font-size: 16px;
                        line-height: 24px;
                    }
                }

                .cart-buttons{
                    display: flex;
                    gap: 10px;
                    @include md{
                        margin-top: 30px;
                    }
                    .quantity-buttons{
                        width: fit-content;
                        display: flex;
                        border: 2px solid rgba(0,0,0,0.2);
                        margin-right: 10px;
                        height: 50px;
                        span{
                            font-size: 18px;
                            width: 40px;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            cursor: pointer;
                            color: #6b6b6b;

                            &:nth-child(1){
                                border-right: 2px solid rgba(0,0,0,0.2);
                            }
                            &:nth-child(2){
                                width: 60px;
                            }
                            &:nth-child(3){
                                border-left: 2px solid rgba(0,0,0,0.2);
                            }
                        }
                    }

                    .add-to-cart-button{
                        outline: 0;
                        border: 0;
                        height: 50px;
                        width: 180px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        cursor: pointer;
                        background:#8e2de2;
                        color: white;
                        font-size: 16px;
                        border-bottom: 3px solid #6516aa;
                        flex-grow: 1;
                        transition: 0.5s all;
                        @include md{
                            flex-grow: unset;
                        }

                       

                        &:nth-child(2){
                            background:#f5f5f5;
                            border: 1px solid black;
                            border-bottom: 3px solid #6516aa;
                            color: #6516aa;
                            transition: 0.5s all;

                            @include md{
                                &:hover{
                                    background:#8e2de2;
                            color: white;
                                }
                            }

                          
                        }
                    }
                }
                .divider{
                    margin: 20px 0;
                    height: 3px;
                    width: 100%;
                    background-color: rgba(0,0,0,0.1);
                }

                
                    .info-item{
                        .text-bold{
                            font-size: 18px;
                            font-weight: 500;
                            display: flex;
                            &:nth-child(1){
                                margin-bottom: 20px;
                            }
                            span{
                                font-size: 16;
                                font-weight: 400;
                                cursor: pointer;
                                color: #6b6b6b;

                            
                                a{
                                    color: #6b6b6b;
                                }

                              

                            }

                            svg{
                                margin: 0 5px;
                                transition: all ease-in-out;
                                transform: scale(1.3);
                                margin: 0 9px;
                                

                                @include md{
                                    transform: scale(1.4);

                                  transform: none;
                                   &:hover{
                                    transform: scale(1.5);
                                    animation: 0.5s;
                                }
                                }
                            }
                           
                        }
                    }
                


            }
        }
    }
}